import React, { useState } from "react";
import styled from "styled-components";
import Android from "../images/android.svg";
import Mac from "../images/ios.svg";
import Nintendo from "../images/nintendo.svg";
import PlayStation from "../images/playstation.svg";
import Xbox from "../images/xbox.svg";
import Linux from "../images/linux.svg";
import PC from "../images/windows.svg";
import { useDispatch, useSelector } from "react-redux";
import {
  getCurrentGame,
  getCurrentScreenShots,
} from "../redux/actions/gamesAction";
import default_image from '../images/default image.jpg'


Card.defaultProps = {
  game:[],
  star:true
}


function Card({ game, star }) {

  const [isHovered, setIsHovered] = useState(false);
  const [isFav, setIsFav] = useState(false);

  const handleMouseOver = () => {
    setIsHovered(true);
  };
  const handleMouseOut = () => {
    setIsHovered(false);
  };

  const gameDispatch = useDispatch();
  const screensDispatch = useDispatch();
  const logosDispatch = useDispatch();

  let i;
  const platformLogos = new Set([]);
  for (i of game.platforms) {
    if (i.platform.name.includes("PC")) {
      platformLogos.add("PC");
    }
    if (i.platform.name.includes("PlayStation")) {
      platformLogos.add("PlayStation");
    }
    if (i.platform.name.includes("Xbox")) {
      platformLogos.add("Xbox");
    }
    if (i.platform.name.includes("Nintendo")) {
      platformLogos.add("Nintendo");
    }
    if (i.platform.name.includes("Mac")) {
      platformLogos.add("Mac");
    }
    if (i.platform.name.includes("Linux")) {
      platformLogos.add("Linux");
    }
    if (i.platform.name.includes("Android")) {
      platformLogos.add("Android");
    } else {
      continue;
    }
  }

  const platformNameArray = [...platformLogos];

  let item;
  const platformLogosArray = [];
  for (item of platformNameArray) {
    if (item === "PC") {
      platformLogosArray.push(<img src={PC} alt="PC" />);
    }
    if (item === "PlayStation") {
      platformLogosArray.push(<img src={PlayStation} alt="PlayStation" />);
    }
    if (item === "Xbox") {
      platformLogosArray.push(<img src={Xbox} alt="Xbox" />);
    }
    if (item === "Nintendo") {
      platformLogosArray.push(<img src={Nintendo} alt="Nintendo" />);
    }
    if (item === "Mac") {
      platformLogosArray.push(<img src={Mac} alt="Mac" />);
    }
    if (item === "Linux") {
      platformLogosArray.push(<img src={Linux} alt="Linux" />);
    }
    if (item === "Android") {
      platformLogosArray.push(<img src={Android} alt="Android" />);
    } else {
      continue;
    }
  }


  const addDispatch = useDispatch();
  const addSelector = useSelector(state => state.gamesReducer.favGames);

  const menuDispatch = useDispatch();

  const handleFav = () => {
    setIsFav(!isFav)
    if (!isFav) {
      addDispatch({type:'ADD_FAV_GAME', payload:[...addSelector,game]})
    } else { 
      const filtered = addSelector.filter(item => item.id !== game.id) 
      addDispatch({type:'ADD_FAV_GAME', payload:filtered})
    }
  }

  return (
    <D0>
      <D
        onMouseOver={handleMouseOver}
        onMouseOut={handleMouseOut}
        isHovered={isHovered}
      >
        <D1>
          <img src={game.background_image || default_image} alt={game.name} />
        </D1>
        <D2>
          <D2_1>
            <D2_1_1>{platformLogosArray}</D2_1_1>
            <D2_1_2>{game.rating ? game.rating : "N"}</D2_1_2>
          </D2_1>
          <D2_2>
            <h2>{game.name}</h2>
          </D2_2>
          <D2_3>
            <S>
              <i className="bi bi-plus-lg"></i>
              <span>{game.suggestions_count}</span>
            </S>
            <Sh style={{ display: `${star && isHovered ? "initial" : "none"}` }} onClick={handleFav}>
              {isFav ? <i className="bi bi-star-fill"></i>:<i className="bi bi-star"></i>}
            </Sh>
            <Sh style={{ display: `${isHovered ? "initial" : "none"}` }}>
              <i className="bi bi-three-dots"></i>
            </Sh>
          </D2_3>
          <D3 style={{ display: `${isHovered ? "initial" : "none"}` }}>
            <D3_1>
              <S1>Release Date:</S1>
              <S2>{game.released}</S2>
            </D3_1>
            <D3_1>
              <S1>Genres:</S1>
              <S2>
                {game.genres.map((genre) => (
                  <Si1 key={genre.id}>{genre.name},</Si1>
                ))}
              </S2>
            </D3_1>
            <D3_2
              onClick={() => {
                gameDispatch(getCurrentGame(game.id));
                screensDispatch(getCurrentScreenShots(game.id));
                logosDispatch({
                  type: "GET_CURRENT_LOGOS",
                  payload: platformLogosArray,
                });
                menuDispatch({
                  type: "MENU",
                  payload: false,
                });
              }}
            >
              <p>Details</p>
              <i className="bi bi-caret-right"></i>
            </D3_2>
          </D3>
        </D2>
      </D>
    </D0>
  );
}

const D0 = styled.div`
  position: relative;
`;

const D = styled.div`
  width: 100%;
  border-radius: 15px;
  background-color: #202020;
  position: ${(props) => (props.isHovered ? "absolute" : "initial")};
  z-index: ${(props) => (props.isHovered ? "50" : "initial")};
`;

const D1 = styled.div`
  img {
    width: 100%;
    height: 50vh;
    object-fit: cover;
    border-radius: 15px 15px 0px 0px;
  }
`;

const D2 = styled.div`
  padding: 1rem;
  padding: 0 auto;
  text-align: center;
`;

const D2_1 = styled.div`
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 1rem;
`;

const D2_1_1 = styled.div`
  img {
    width: 2rem;
    height: 2rem;
    margin-right: 0.5rem;
    border-radius: 5px;
  }
`;

const D2_1_2 = styled.div`
  border: 2px solid green;
  border-radius: 5px;
  padding: 0.25rem 0.5rem;
`;

const D2_2 = styled.div`
  margin-bottom: 2rem;
`;

const D2_3 = styled.div`
  margin-bottom: 1.5rem;
`;

const S = styled.span`
  background-color: grey;
  padding: 0.25rem 0.5rem;
  border-radius: 5px;
  margin-right: 0.25rem;
  cursor: pointer;
  span {
    padding-left: 5px;
  }
`;

const Sh = styled.span`
  background-color: grey;
  padding: 0.25rem 0.5rem;
  border-radius: 5px;
  margin-right: 0.25rem;
  cursor: pointer;
  span {
    padding-left: 5px;
  }
`;

const D3 = styled.div``;

const D3_1 = styled.div`
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 1rem 0rem;
`;

const S1 = styled.span``;

const S2 = styled.span`
  text-decoration: underline;
  overflow: auto;
  margin-left: 0.5rem;
  &::-webkit-scrollbar {
    height: 2px;
  }
`;

const Si1 = styled.span`
  margin-right: 0.25rem;
`;

const D3_2 = styled.div`
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 0.25rem 0rem;
  padding: 0.5rem 0rem;
  background-color: blue;
  border-radius: 10px;
  cursor: pointer;
  p {
    padding-right: 0.5rem;
  }
`;

export default Card;
