import React, { } from "react";
import styled from "styled-components";
import playstation from "../images/playstation.svg";
import xbox from "../images/xbox.svg";
import action from "../images/action.png";
import strategy from "../images/strategy.png";
import RPG from "../images/RPG.png";
import { useSelector } from "react-redux";
import { Link } from "react-router-dom";

function Sidebar() {
  let showMenuSelector = useSelector((state) => state.gamesReducer.menu);
  let showStatus;
  if (showMenuSelector) {
    showStatus = "normal";
  } else {
    showStatus = "none";
  }

  return (
    <D showStatus={showStatus}>
      <Link to='/' style={{color:"white", textDecoration:"none"}}><h1>Home</h1></Link>
      <D1>
        <D1_1>
          <h1>SweetLine</h1>
          <D1_1_1>
            <h2>sz</h2>
          </D1_1_1>
        </D1_1>
        <D1_2>
          <D1_2_1>
            <D1_2_1_1>
              <i className="bi bi-collection"></i>
            </D1_2_1_1>
            <Link to="/library" style={{color:"white", textDecoration:"none"}}>
              <h4>my library</h4>
            </Link>
          </D1_2_1>
          <D1_2_1>
            <D1_2_1_1>
              <i className="bi bi-people-fill"></i>
            </D1_2_1_1>
            <h4>people you follow</h4>
          </D1_2_1>
          <D1_2_1>
            <D1_2_1_1>
              <i className="bi bi-gift-fill"></i>
            </D1_2_1_1>
            <h4>wish list</h4>
          </D1_2_1>
        </D1_2>
      </D1>
      <D1>
        <D1_1>
          <h1>New Releases</h1>
        </D1_1>
        <D1_2>
          <D1_2_1>
            <D1_2_1_1>
              <i className="bi bi-star-fill"></i>
            </D1_2_1_1>
            <h4>last 30 days</h4>
          </D1_2_1>
          <D1_2_1>
            <D1_2_1_1>
              <i className="bi bi-calendar-day-fill"></i>
            </D1_2_1_1>
            <h4>this week</h4>
          </D1_2_1>
          <D1_2_1>
            <D1_2_1_1>
              <i className="bi bi-calendar-date"></i>
            </D1_2_1_1>
            <h4>next week</h4>
          </D1_2_1>
          <D1_2_1>
            <D1_2_1_1>
              <i className="bi bi-calendar-event"></i>
            </D1_2_1_1>
            <h4>release calender</h4>
          </D1_2_1>
        </D1_2>
      </D1>
      <D1>
        <D1_1>
          <h1>Top</h1>
        </D1_1>
        <D1_2>
          <D1_2_1>
            <D1_2_1_1>
              <i className="bi bi-trophy"></i>
            </D1_2_1_1>
            <h4>best of the year</h4>
          </D1_2_1>
          <D1_2_1>
            <D1_2_1_1>
              <i className="bi bi-bar-chart-fill"></i>
            </D1_2_1_1>
            <h4>popular in 2020</h4>
          </D1_2_1>
          <D1_2_1>
            <D1_2_1_1>
              <i className="bi bi-stars"></i>
            </D1_2_1_1>
            <h4>all time top 250</h4>
          </D1_2_1>
        </D1_2>
      </D1>
      <h1>All Games</h1>
      <D1>
        <D1_1>
          <h1>brows</h1>
        </D1_1>
        <D1_2>
          <D1_2_1>
            <D1_2_1_1>
              <i className="bi bi-controller"></i>
            </D1_2_1_1>
            <h4>platforms</h4>
          </D1_2_1>
          <D1_2_1>
            <D1_2_1_1>
              <i className="bi bi-file-arrow-down-fill"></i>
            </D1_2_1_1>
            <h4>stores</h4>
          </D1_2_1>
          <D1_2_1>
            <D1_2_1_1>
              <i className="bi bi-archive"></i>
            </D1_2_1_1>
            <h4>collections</h4>
          </D1_2_1>
          <D1_2_1>
            <D1_2_1_1>
              <i className="bi bi-arrow-down-square"></i>
            </D1_2_1_1>
            <h4>show all</h4>
          </D1_2_1>
        </D1_2>
      </D1>
      <D1>
        <D1_1>
          <h1>Platforms</h1>
        </D1_1>
        <D1_2>
          <D1_2_1>
            <D1_2_1_1>
              <i className="bi bi-laptop"></i>
            </D1_2_1_1>
            <h4>pc</h4>
          </D1_2_1>
          <D1_2_1>
            <D1_2_1_1>
              <img src={playstation} alt="playstation" />
            </D1_2_1_1>
            <h4>play station 4</h4>
          </D1_2_1>
          <D1_2_1>
            <D1_2_1_1>
              <img src={xbox} alt="xbox" />
            </D1_2_1_1>
            <h4>xbox one</h4>
          </D1_2_1>
        </D1_2>
      </D1>
      <D1>
        <D1_1>
          <h1>Genres</h1>
        </D1_1>
        <D1_2>
          <D1_2_1>
            <D1_2_1_1>
              <img src={action} alt="action" />
            </D1_2_1_1>
            <h4>action</h4>
          </D1_2_1>
          <D1_2_1>
            <D1_2_1_1>
              <img src={strategy} alt="strategy" />
            </D1_2_1_1>
            <h4>strategy</h4>
          </D1_2_1>
          <D1_2_1>
            <D1_2_1_1>
              <img src={RPG} alt="RPG" />
            </D1_2_1_1>
            <h4>RPG</h4>
          </D1_2_1>
        </D1_2>
      </D1>
    </D>
  );
}


const D = styled.div`
  display: ${(props) => props.showStatus};
  position: sticky;
  top: 10vh;
  left: 0rem;
  z-index: 200;
  padding: 3rem 0.75rem 0 0;
  height: calc(100vh - 10vh);
  overflow-y: auto;
  ::-webkit-scrollbar {
    width: 5px;
  }
  ::-webkit-scrollbar-thumb {
    background-color: red;
    border-radius: 2.5px;
  }
  h1 {
    margin-bottom: 1rem;
  }

  @media (max-width: 768px) {
    width: content;
    position: fixed;
    left: 1rem;
    z-index: 300;
    background-color: black;
  }
`;

const D1 = styled.div``;

const D1_1 = styled.div`
  display: flex;
  align-items: center;
  position: relative;
`;

const D1_1_1 = styled.div`
  width: 2.5rem;
  height: 2.5rem;
  background-image: linear-gradient(to right, #3a0ca3, #4361ee);
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 50%;
  margin-left: 0.5rem;
  align-self: center;
  position: absolute;
  top: 0;
  right: 0;
  h2 {
    background-color: transparent;
  }
`;

const D1_2 = styled.div`
  padding-bottom: 1rem;
`;

const D1_2_1 = styled.div`
  display: flex;
  align-items: center;
  margin-bottom: 0.25rem;
`;

const D1_2_1_1 = styled.div`
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #202020;
  padding: 0.25rem;
  margin-right: 0.5rem;
  border-radius: 5px;
  i {
    font-size: 1.5rem;
    background-color: transparent;
    display: inline-flex;
  }
  img {
    width: 1.5rem;
  }
`;

export default Sidebar;
