import { GET_POPULAR_GAMES, GET_NEW_GAMES, GET_UPCOMING_GAMES, GET_SEARCHED_GAMES, SET_LOADING, GAMES_ERROR } from '../types';
import { popular_games, new_games, upcoming_games, searched_games, game, screenshots} from '../api'
 
export const getPopularGames = () => async dispatch => {
    try {
        setLoading();
        const popularGamesData = await fetch(popular_games())
        const popularGames = await popularGamesData.json()

        dispatch({
            type: GET_POPULAR_GAMES,
            payload: popularGames
        });
    }
    catch (err) {
        dispatch({
            type: GAMES_ERROR,
            payload: err.response.data
        })
    }
}

