import {
  GET_POPULAR_GAMES,
  GET_UPCOMING_GAMES,
  GET_NEW_GAMES,
  GET_SEARCHED_GAMES,
  SET_LOADING,
  GAMES_ERROR,
} from "../types";

const initialState = {
  upcomingGames: "",
  newGames: "",
  popularGames: "",
  searchedGames: "",
  favGames: [],
  menu: false,
  current: "",
  currentScreenShots: "",
  currentLogos: "",
  loading: false,
  error: "",
};

const gamesReducer = (state = initialState, action) => {
  switch (action.type) {
    case SET_LOADING:
      return {
        ...state,
        loading: true,
      };
    case GET_POPULAR_GAMES:
      return {
        ...state,
        popularGames: action.payload,
        loading: false,
      };
    case GET_UPCOMING_GAMES:
      return {
        ...state,
        upcomingGames: action.payload,
        loading: false,
      };
    case GET_NEW_GAMES:
      return {
        ...state,
        newGames: action.payload,
        loading: false,
      };
    case GET_SEARCHED_GAMES:
      return {
        ...state,
        searchedGames: action.payload,
        loading: false,
      };
    case "GET_CURRENT_GAME":
      return {
        ...state,
        current: action.payload,
        loading: false,
      };
    case "GET_CURRENT_SCREENSHOTS":
      return {
        ...state,
        currentScreenShots: action.payload,
        loading: false,
      };
    case "GET_CURRENT_LOGOS":
      return {
        ...state,
        currentLogos: action.payload,
      };
    case "CLEAR_SEARCHED_GAMES":
      return {
        ...state,
        searchedGames: action.payload,
        loading: false,
      };
    case "CLEAR_CURRENT_GAME":
      return {
        ...state,
        current: "",
      };
    case "ADD_FAV_GAME":
      return {
        ...state,
        favGames: action.payload
      };
    case "MENU":
      return {
        ...state,
        menu: action.payload,
        loading: false,
      };
    case GAMES_ERROR:
      return {
        ...state,
        error: action.payload,
      };
    default:
      return state;
  }
};

export default gamesReducer;
