import React, { useState } from "react";
import styled from "styled-components";
import { useDispatch, useSelector } from "react-redux";
import { getSearchedGames } from "../redux/actions/gamesAction";
import { Hidden } from "react-grid-system";
import { Link } from 'react-router-dom'

function Component() {
  const [searchValue, setSearchValue] = useState("");
  const [submittedValue, setSubmittedValue] = useState("");

  const getSearchedGamesDispatch = useDispatch();

  const handleChange = (e) => {
    setSearchValue(e.target.value);
  };
  const handleSubmit = (e) => {
    e.preventDefault();
    setSubmittedValue(searchValue);
    setSearchValue("");
    getSearchedGamesDispatch(getSearchedGames(submittedValue));
  };

  const menuShowDispatch = useDispatch()
  const menuShowSelector = useSelector(state => state.gamesReducer.menu)

  return (
    <D>
      <D1>
        <Link to='/' style={{color:"white", textDecoration:"none"}}><h2>Games</h2></Link>
      </D1>
      <D2>
        <form onSubmit={handleSubmit}>
          <input
            onChange={handleChange}
            value={searchValue}
            type="search"
            placeholder="search 6583 games"
          />
        </form>
        <i className="bi bi-search"></i>
      </D2>
      <Hidden sm xs>
        <D3>
          <D3_1>
            <h2>sz</h2>
          </D3_1>
          <Link to='/library' style={{color:"white", textDecoration:"none"}}><p>my library</p></Link>
          <i className="bi bi-bell"></i>
          <i className="bi bi-plus-lg"></i>
        </D3>
      </Hidden>
        <D3_1 onClick={() => menuShowDispatch({type:'MENU', payload:!menuShowSelector})}>
          <i className="bi bi-list"></i>
        </D3_1>
    </D>
  );
}

const D = styled.div`
  background-color: black;
  z-index: 100;
  position: sticky;
  top: 0;
  left: 0;
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 10vh;
  border-bottom: 1px solid white;
`;
const D1 = styled.div`
  width: 10vw;
  text-align: center;
`;
const D2 = styled.div`
  width: 60vw;
  padding: 0 0.5rem;
  position: relative;
  :hover {
    input {
      background-color: white;
      color: black;
      ::placeholder {
        color: black;
        opacity: 1;
      }
    }
    i {
      color: black;
    }
  }
  input {
    color: white;
    width: 100%;
    height: 2.5rem;
    border-radius: 25px;
    padding: 0rem 1rem 0rem 2rem;
    outline: none;
    background-color: #202020;
    border: 2px solid transparent;
    ::placeholder {
      color: white;
      opacity: 1;
    }
  }
  i {
    position: absolute;
    top: 0.67rem;
    left: 1.25rem;
    background-color: transparent;
  }

  @media (max-width: 768px) {
    margin-bottom: 0.25rem;
  }
`;
const D3 = styled.div`
  display: flex;
  justify-content: space-around;
  align-items: center;
  width: 30vw;
  a {
    i {
      font-size: 1.5rem;
      font-weight: bold;
    }
  }
`;
const D3_1 = styled.div`
  width: 2.5rem;
  height: 2.5rem;
  background-image: linear-gradient(to right, #3a0ca3, #4361ee);
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 50%;
  cursor: pointer;
  h2 {
    background-color: transparent;
  }
`;

export default Component;
