import React, { useState, useEffect, useRef } from "react";
import styled from "styled-components";
import { useSelector, useDispatch } from "react-redux";
import Card from "../components/Card";




function MainPage() {
  const [orderListAppear, setOrderListAppear] = useState(false);
  const [platformListAppear, setPlatformListAppear] = useState(false);
  const [isPopular, setIsPopular] = useState(true);
  const [isUpcoming, setIsUpcoming] = useState(false);
  const [isNew, setIsNew] = useState(false);

  const [isSpecific, setIsSpecific] = useState(false);

  const [isPcSpecific, setIsPcSpecific] = useState(false);
  const [isPlaystationSpecific, setIsPlaystationSpecific] = useState(false);
  const [isXboxSpecific, setIsXboxSpecific] = useState(false);
  const [isMacSpecific, setIsMacSpecific] = useState(false);
  const [isAndroidSpecific, setIsAndroidSpecific] = useState(false);
  const [isLinuxSpecific, setIsLinuxSpecific] = useState(false);
  const [isNintendoSpecific, setIsNintendoSpecific] = useState(false);

  const [isSingleShow, setIsSingleShow] = useState(false);

  const popSelector = useSelector((state) => state.gamesReducer.popularGames);
  const upSelector = useSelector((state) => state.gamesReducer.upcomingGames);
  const newSelector = useSelector((state) => state.gamesReducer.newGames);
  const searchedSelector = useSelector(
    (state) => state.gamesReducer.searchedGames
  );
  
  const currentGameSelector = useSelector((state) => state.gamesReducer.current);
  const currentScreenShotsSelector = useSelector((state) => state.gamesReducer.currentScreenShots);
  const currentLogosSelector = useSelector((state) => state.gamesReducer.currentLogos);

  const clearSearchGamesDispatch = useDispatch();
  const clearCurrentGamesDispatch = useDispatch();
  

  //make platform specific arrays
  let popPcArray = [];
  let popPlaystationArray = [];
  let popXboxArray = [];
  let popMacArray = [];
  let popAndroidArray = [];
  let popLinuxArray = [];
  let popNintendoArray = [];
  let popItem;
  if (popSelector.results) {
    popSelector.results.forEach((game) => {
      for (popItem of game.platforms) {
        if (popItem.platform.name.includes("PC")) {
          popPcArray.push(game);
        }
        else if (popItem.platform.name.includes("PlayStation 4")) {
          popPlaystationArray.push(game);
        }
        else if (popItem.platform.name.includes("Xbox One")) {
          popXboxArray.push(game);
        }
        else if (popItem.platform.name.includes("Nintendo")) {
          popNintendoArray.push(game);
        }
        else if (popItem.platform.name.includes("Mac")) {
          popMacArray.push(game);
        }
        else if (popItem.platform.name.includes("Linux")) {
          popLinuxArray.push(game);
        }
        else if (popItem.platform.name.includes("Android")) {
          popAndroidArray.push(game);
        } else {
          continue;
        }
      }
    });
  }

  let upcomingPcArray = [];
  let upcomingPlaystationArray = [];
  let upcomingXboxArray = [];
  let upcomingMacArray = [];
  let upcomingAndroidArray = [];
  let upcomingLinuxArray = [];
  let upcomingNintendoArray = [];
  let upcomingItem;
  if (upSelector.results) {
    upSelector.results.forEach((game) => {
      for (upcomingItem of game.platforms) {
        if (upcomingItem.platform.name.includes("PC")) {
          upcomingPcArray.push(game);
        }
        else if (upcomingItem.platform.name.includes("PlayStation 4")) {
          upcomingPlaystationArray.push(game);
        }
        else if (upcomingItem.platform.name.includes("Xbox One")) {
          upcomingXboxArray.push(game);
        }
        else if (upcomingItem.platform.name.includes("Nintendo")) {
          upcomingNintendoArray.push(game);
        }
        else if (upcomingItem.platform.name.includes("Mac")) {
          upcomingMacArray.push(game);
        }
        else if (upcomingItem.platform.name.includes("Linux")) {
          upcomingLinuxArray.push(game);
        }
        else if (upcomingItem.platform.name.includes("Android")) {
          upcomingAndroidArray.push(game);
        } else {
          continue;
        }
      }
    });
  }

  let newPcArray = [];
  let newPlaystationArray = [];
  let newXboxArray = [];
  let newMacArray = [];
  let newAndroidArray = [];
  let newLinuxArray = [];
  let newNintendoArray = [];
  let newItem;
  if (newSelector.results) {
    newSelector.results.forEach((game) => {
      for (newItem of game.platforms) {
        if (newItem.platform.name.includes("PC")) {
          newPcArray.push(game);
        }
        else if (newItem.platform.name.includes("PlayStation 4")) {
          newPlaystationArray.push(game);
        }
        else if (newItem.platform.name.includes("Xbox One")) {
          newXboxArray.push(game);
        }
        else if (newItem.platform.name.includes("Nintendo")) {
          newNintendoArray.push(game);
        }
        else if (newItem.platform.name.includes("Mac")) {
          newMacArray.push(game);
        }
        else if (newItem.platform.name.includes("Linux")) {
          newLinuxArray.push(game);
        }
        else if (newItem.platform.name.includes("Android")) {
          newAndroidArray.push(game);
        } else {
          continue;
        }
      }
    });
  }

  const useHandleClickOutside = (ref, element) => {
    useEffect(() => {
      document.addEventListener("mousedown", handleClickOutside);
      return () => {
        document.removeEventListener("mousedown", handleClickOutside);
      };

      function handleClickOutside(event) {
        if (ref.current && !ref.current.contains(event.target)) {
          switch (element) {
            case 1:
              setOrderListAppear(false);
              break;
            case 2:
              setPlatformListAppear(false);
              break;
            default:
              break;
          }
        }
      }
    }, [ref, element]);
  };

  const wrapperRef1 = useRef(null);
  const wrapperRef2 = useRef(null);
  useHandleClickOutside(wrapperRef1, 1);
  useHandleClickOutside(wrapperRef2, 2);

  const handleGameShow = (Pop, New, Upcoming) => {
    setIsPopular(Pop);
    setIsUpcoming(New);
    setIsNew(Upcoming);
    clearSearchGamesDispatch({ type: "CLEAR_SEARCHED_GAMES", payload: "" });
    setOrderListAppear(false);
  };

  const handleFilteredGameShow = (
    pc,
    playstation,
    xbox,
    nintendo,
    mac,
    linux,
    android
  ) => {
    setIsSpecific(true);
    setIsPcSpecific(pc);
    setIsPlaystationSpecific(playstation);
    setIsXboxSpecific(xbox);
    setIsNintendoSpecific(nintendo);
    setIsMacSpecific(mac);
    setIsLinuxSpecific(linux);
    setIsAndroidSpecific(android);
    clearSearchGamesDispatch({ type: "CLEAR_SEARCHED_GAMES", payload: "" });
    setPlatformListAppear(false);
  };

  let orderLabel;
  if (isPopular) {
    orderLabel = "popular";
  } else if (isNew) {
    orderLabel = "new";
  } else if (isUpcoming) {
    orderLabel = "upcoming";
  } else {
    orderLabel = "";
  }

  let platformLabel = "";
  if (!isSpecific) {
    platformLabel = "all";
  } else if (isPcSpecific) {
    platformLabel = "pc";
  } else if (isPlaystationSpecific) {
    platformLabel = "playStation";
  } else if (isXboxSpecific) {
    platformLabel = "Xbox";
  } else if (isNintendoSpecific) {
    platformLabel = "Nintendo";
  } else if (isMacSpecific) {
    platformLabel = "Mac";
  } else if (isLinuxSpecific) {
    platformLabel = "Linux";
  } else if (isAndroidSpecific) {
    platformLabel = "Android";
  } else {
    platformLabel = "";
  }

  if (searchedSelector) {
    orderLabel = "search";
    platformLabel = "";
  }

  const htmlString = currentGameSelector ? currentGameSelector.description:'null'

  return (
    <D>
      <P>Top Picks</P>
      <P1>based on your ratings</P1>
      <D1>

            <D1_1
              onClick={() => setOrderListAppear(!orderListAppear)}
              ref={wrapperRef1}
            >
              <p>
                order by : <span>{orderLabel}</span>
                <i className="bi bi-caret-down"></i>
              </p>
              {orderListAppear && (
                <D1S1 onClick={(e) => e.stopPropagation()}>
                  <ul>
                    <li
                      onClick={() => handleGameShow(true, false, false, false)}
                    >
                      popular games
                    </li>
                    <li
                      onClick={() => handleGameShow(false, false, true, false)}
                    >
                      new games
                    </li>
                    <li
                      onClick={() => handleGameShow(false, true, false, false)}
                    >
                      upcoming games
                    </li>
                  </ul>
                </D1S1>
              )}
            </D1_1>

            <D1_1
              onClick={() => setPlatformListAppear(!platformListAppear)}
              ref={wrapperRef2}
            >
              <p>
                platforms : <span>{platformLabel}</span>{" "}
                <i className="bi bi-caret-down"></i>
              </p>
              {platformListAppear && (
                <D1S2 onClick={(e) => e.stopPropagation()}>
                  <ul>
                    <li
                      onClick={() => {
                        setIsSpecific(false);
                        setPlatformListAppear(false);
                      }}
                    >
                      None
                    </li>
                    <li
                      onClick={() =>
                        handleFilteredGameShow(
                          true,
                          false,
                          false,
                          false,
                          false,
                          false,
                          false
                        )
                      }
                    >
                      PC
                    </li>
                    <li
                      onClick={() =>
                        handleFilteredGameShow(
                          false,
                          true,
                          false,
                          false,
                          false,
                          false,
                          false
                        )
                      }
                    >
                      PlayStation
                    </li>
                    <li
                      onClick={() =>
                        handleFilteredGameShow(
                          false,
                          false,
                          true,
                          false,
                          false,
                          false,
                          false
                        )
                      }
                    >
                      Xbox
                    </li>
                    <li
                      onClick={() =>
                        handleFilteredGameShow(
                          false,
                          false,
                          false,
                          true,
                          false,
                          false,
                          false
                        )
                      }
                    >
                      Nintendo
                    </li>
                    <li
                      onClick={() =>
                        handleFilteredGameShow(
                          false,
                          false,
                          false,
                          false,
                          true,
                          false,
                          false
                        )
                      }
                    >
                      Mac
                    </li>
                    <li
                      onClick={() =>
                        handleFilteredGameShow(
                          false,
                          false,
                          false,
                          false,
                          false,
                          true,
                          false
                        )
                      }
                    >
                      Linux
                    </li>
                    <li
                      onClick={() =>
                        handleFilteredGameShow(
                          false,
                          false,
                          false,
                          false,
                          false,
                          false,
                          true
                        )
                      }
                    >
                      Android
                    </li>
                  </ul>
                </D1S2>
              )}
            </D1_1>

            <D1_2>
              <i className="bi bi-border-all" onClick={() => setIsSingleShow(false)}></i>
              <i className="bi bi-bookshelf" onClick={() => setIsSingleShow(true)}></i>
            </D1_2>

      </D1>
      <GamesGridContainer isSingleShow={isSingleShow}>
            {searchedSelector &&
              searchedSelector.results.map((game) => (

                  <Card key={game.id} game={game} />

              ))}
            {!searchedSelector &&
              isPopular &&
              !isSpecific &&
              popSelector &&
              popSelector.results.map((game) => (

                  <Card key={game.id} game={game} />

              ))}
            {!searchedSelector &&
              isUpcoming &&
              !isSpecific &&
              upSelector &&
              upSelector.results.map((game) => (

                  <Card key={game.id} game={game} />

              ))}
            {!searchedSelector &&
              isNew &&
              !isSpecific &&
              newSelector &&
              newSelector.results.map((game) => (

                  <Card key={game.id} game={game} />

              ))}

            {/* ehtemalat new */}
            {!searchedSelector &&
              isNew &&
              isPcSpecific &&
              newSelector &&
              newPcArray.map((game) => (

                  <Card key={game.id} game={game} />

              ))}
            
            
            {!searchedSelector &&
              isNew &&
              isXboxSpecific &&
              newSelector &&
              newXboxArray.map((game) => (

                  <Card key={game.id} game={game} />

              ))}
            {!searchedSelector &&
              isNew &&
              isMacSpecific &&
              newSelector &&
              newMacArray.map((game) => (

                  <Card key={game.id} game={game} />

              ))}
            {!searchedSelector &&
              isNew &&
              isAndroidSpecific &&
              newSelector &&
              newAndroidArray.map((game) => (

                  <Card key={game.id} game={game} />

              ))}
            {!searchedSelector &&
              isNew &&
              isLinuxSpecific &&
              newSelector &&
              newLinuxArray.map((game) => (

                  <Card key={game.id} game={game} />

              ))}
            {!searchedSelector &&
              isNew &&
              isNintendoSpecific &&
              newSelector &&
              newNintendoArray.map((game) => (

                  <Card key={game.id} game={game} />

              ))}

            {/* ehtemalat popular */}
            {!searchedSelector &&
              isPopular &&
              isPcSpecific &&
              newSelector &&
              popPcArray.map((game) => (

                  <Card key={game.id} game={game} />

              ))}
            {!searchedSelector &&
              isPopular &&
              isPlaystationSpecific &&
              newSelector &&
              popPlaystationArray.map((game) => (

                  <Card key={game.id} game={game} />

              ))}
            {!searchedSelector &&
              isPopular &&
              isXboxSpecific &&
              newSelector &&
              popXboxArray.map((game) => (

                  <Card key={game.id} game={game} />

              ))}
            {!searchedSelector &&
              isPopular &&
              isMacSpecific &&
              newSelector &&
              popMacArray.map((game) => (

                  <Card key={game.id} game={game} />

              ))}
            {!searchedSelector &&
              isPopular &&
              isAndroidSpecific &&
              newSelector &&
              popAndroidArray.map((game) => (

                  <Card key={game.id} game={game} />

              ))}
            {!searchedSelector &&
              isPopular &&
              isLinuxSpecific &&
              newSelector &&
              popLinuxArray.map((game) => (

                  <Card key={game.id} game={game} />

              ))}
            {!searchedSelector &&
              isPopular &&
              isNintendoSpecific &&
              newSelector &&
              popNintendoArray.map((game) => (

                  <Card key={game.id} game={game} />

              ))}

            {/* ehtemalat upcoming */}
            {!searchedSelector &&
              isUpcoming &&
              isPcSpecific &&
              newSelector &&
              upcomingPcArray.map((game) => (

                  <Card key={game.id} game={game} />

              ))}
            {!searchedSelector &&
              isUpcoming &&
              isPlaystationSpecific &&
              newSelector &&
              upcomingPlaystationArray.map((game) => (

                  <Card key={game.id} game={game} />

              ))}
            {!searchedSelector &&
              isUpcoming &&
              isXboxSpecific &&
              newSelector &&
              upcomingXboxArray.map((game) => (

                  <Card key={game.id} game={game} />

              ))}
            {!searchedSelector &&
              isUpcoming &&
              isMacSpecific &&
              newSelector &&
              upcomingMacArray.map((game) => (

                  <Card key={game.id} game={game} />

              ))}
            {!searchedSelector &&
              isUpcoming &&
              isAndroidSpecific &&
              newSelector &&
              upcomingAndroidArray.map((game) => (

                  <Card key={game.id} game={game} />

              ))}
            {!searchedSelector &&
              isUpcoming &&
              isLinuxSpecific &&
              newSelector &&
              upcomingLinuxArray.map((game) => (

                  <Card key={game.id} game={game} />

              ))}
            {!searchedSelector &&
              isUpcoming &&
              isNintendoSpecific &&
              newSelector &&
              upcomingNintendoArray.map((game) => (

                  <Card key={game.id} game={game} />

              ))}
      </GamesGridContainer>
      <PE>Nothing else here ...</PE>
      {currentGameSelector && (
        <Shadow onClick={(e) => { e.target.classList.contains('target') && clearCurrentGamesDispatch({type:'CLEAR_CURRENT_GAME'})}} className='target'>
        <Modal>
           <DM1>
              {currentScreenShotsSelector && currentScreenShotsSelector.results.map((item) => <ModalImg key={item.id} src={item.image} alt='name' />)}
           </DM1>
           <DM2>
             <h1>{currentGameSelector.name}</h1>
             <div>
              {currentLogosSelector.map((item,index) => <ModalLogo key={index} src={item.props.src}/>)}
             </div>
             <DataGrid>             
              <div>
                <h3>rating:</h3>
                <p>{currentGameSelector.rating}</p>
              </div>
              <div>
                <h3>genres:</h3>
                <p>{currentGameSelector.genres.map(item => <span key={item.id}>{item.name} | </span>)}</p>
              </div>
              <div>
                <h3>developers:</h3>
                <p>{currentGameSelector.developers.map(item => <span key={item.id}>{item.name} | </span>)}</p>
              </div>
              <div>
                <h3>released:</h3>
                <p>{currentGameSelector.released}</p>
              </div>
             </DataGrid>
             <div>
                {currentGameSelector.clip && <ModalVid src={currentGameSelector.clip}></ModalVid>}
                {currentGameSelector.background_image && <ModalMainImg src={currentGameSelector.background_image}/>}
             </div>
             <Description>
               <h3>description:</h3>
               <div dangerouslySetInnerHTML={{ __html: htmlString, }} />
             </Description>
           </DM2>
        </Modal>
      </Shadow>
      )
      }
      
    </D>
  );
}

const PE = styled.p`
  font-size: 3rem;
  margin:45rem 0 2rem 0;
`

const Description = styled.div`
  margin: 0.5rem 0;
  text-align: justify;
  word-wrap: break-word;
`

const DataGrid = styled.div`
  display:grid;
  grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
  margin: 0.5rem 0;
`

const ModalVid = styled.video`
  width:100%;
  display:block;
  border-radius: 5px;
`

const ModalMainImg = styled.img`
  width:100%;
  display:block;
  border-radius: 5px;
`

const ModalLogo = styled.img`
  width:2rem;
  margin:0.25rem;
  border-radius: 5px;
`

const ModalImg = styled.img`
  width:100%;
  display:block;
`

const DM1 = styled.div`
  width:50%;
  overflow-y:auto;
  &::-webkit-scrollbar {
    width: 5px;
  }
  @media (max-width:786px) {
    width:100%;
  }
`
const DM2 = styled.div`
  width:50%;
  padding:0.5rem;
  overflow-y: auto;
  &::-webkit-scrollbar {
  width: 5px;
  }
  @media (max-width: 786px) {
    width:100%;
  }
`

const Modal = styled.div`
  position: absolute;
  top:75px;
  left:5%;
  width:90%;
  height:85vh;
  background-color: white;
  color:black;
  display:flex;
  z-index:100;
  @media (max-width: 786px) {
    flex-direction: column-reverse;
  }
`

const Shadow = styled.div`
  position: fixed;
  top:0;
  left:0;
  width:100%;
  height:100%;
  background-color: rgba(0,0,0,0.5);
  display:flex;
  justify-content: center;
  align-items: center;
  z-index:100;
`


const GamesGridContainer = styled.div`
  display: grid;
  grid-template-columns: ${props => props.isSingleShow ? 'repeat(auto-fit, 50%)':'repeat(auto-fit, minmax(250px,1fr))'};
  justify-content: center;
  gap: 2rem 2rem;
  margin-top: 2rem;
`

const D = styled.div`
`;

const P = styled.p`
  font-size: 4rem;
  font-weight: bold;
`;
const P1 = styled.p`
  margin-bottom: 3rem;
`;

const D1 = styled.div`
  display: flex;
  align-items: center;

  @media (max-width: 768px) {
    flex-direction:column;
}

`;
const D1_1 = styled.div`
  display: flex;
  align-items: center;
  background-color: #202020;
  padding: 0.5rem;
  border-radius: 5px;
  margin-right: 0.25rem;
  position: relative;
  width: 14rem;
  cursor: pointer;
  p,
  span,
  i {
    background-color: transparent;
  }
  i {
    position: absolute;
    top: 0.5rem;
    right: 0.5rem;
  }

  @media (max-width: 768px) {
    margin-bottom:0.5rem;
}
`;
const D1S1 = styled.div`
  z-index: 100;
  position: absolute;
  top: 0;
  left: 0;
  padding: 0.5rem;
  background-color: white;
  border-radius: 5px;
  ul {
    list-style: none;
    background-color: white;
  }
  li {
    font-size: 0.8rem;
    background-color: white;
    color: black;
    border-radius: 5px;
    padding: 0.5rem;
    :hover {
      background-color: lightgray;
    }
  }
`;

const D1S2 = styled.div`
  z-index: 100;
  position: absolute;
  top: 0rem;
  left: 0;
  padding: 0.5rem;
  background-color: white;
  border-radius: 5px;
  ul {
    list-style: none;
    background-color: white;
  }
  li {
    font-size: 0.8rem;
    background-color: white;
    color: black;
    border-radius: 5px;
    padding: 0.5rem;
    :hover {
      background-color: lightgray;
    }
  }
`;

const D1_2 = styled.div`
  display: flex;
  align-items: center;
  margin-left: auto;
  p {
    margin-right: 0.5rem;
  }
  i {
    display: inline-flex;
    background-color: #202020;
    padding: 0.5rem;
    margin: 0 0.25rem;
    border-radius: 5px;
    font-size: 2rem;
    cursor: pointer;
  }
  @media (max-width: 768px) {
    margin-left:0rem;
}
`;

export default MainPage;
